*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 1rem;
  background-color: #f1f1f1;
}

input{
  width: 100%;
  font-size: 1.2rem;
  color: gray;
}

.card__grid{
  display: grid;
  align-items: flex-start;
  justify-items: center;
  grid-gap: 1.5rem;
  grid-template-columns: repeat( auto-fit, minmax( 19rem, 1fr));
}

.card__item{
  align-items: center;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 1px 10px 3px rgba(128, 128, 128, 0.294);
  display: flex;
  flex-direction: column;
  height: fit-content;
  overflow: hidden;
  text-align: center;
  width: 100%;
}

.card__img{
  object-fit: cover;
  height: 17rem;
}